.hana-button-error, .hana-button-primary, .hana-button-warning, .hana-button-disabled {
  border: 0;
  color: #fff; }
  .hana-button-error i[class^='hanaicon'], .hana-button-primary i[class^='hanaicon'], .hana-button-warning i[class^='hanaicon'], .hana-button-disabled i[class^='hanaicon'] {
    color: #fff; }

.hana-button {
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
  cursor: pointer;
  display: inline-block;
  font-size: 12px;
  white-space: nowrap;
  height: 22px;
  line-height: 0;
  padding: 0 10px;
  transition: border-color 0.25s ease-out, color 0.25s ease-out, background-color 0.25s ease-out, position 0.25s ease-out; }
  .hana-button:focus {
    outline: none; }

.hana-button-default {
  border: 1px solid #ccd0d7;
  color: #ccc; }
  .hana-button-default i[class^='hanaicon'] {
    color: #ccc; }
  .hana-button-default:hover {
    border-color: #bfbfbf;
    color: #bfbfbf;
    z-index: 2; }

.hana-button-error {
  background-color: #d56767;
  padding: 15px 18px; }
  .hana-button-error:hover {
    background-color: #cf5353; }

.hana-button-primary {
  background-color: #6cf;
  padding: 15px 18px; }
  .hana-button-primary:hover {
    background-color: #4dc4ff; }

.hana-button-warning {
  background-color: #e8964a;
  padding: 15px 18px; }
  .hana-button-warning:hover {
    background-color: #e58933; }

.hana-button-disabled {
  background-color: #ccd0d7;
  padding: 15px 18px; }
  .hana-button-disabled:hover {
    cursor: not-allowed; }

.hana-button-small {
  font-size: 12px;
  padding: 0 10px; }

.hana-button-middle {
  font-size: 14px;
  padding: 14px 18px; }

.hana-button-large {
  font-size: 18px;
  padding: 20px 28px; }

.hana-button-label {
  display: inline-flex; }
  .hana-button-label .button-label {
    cursor: default; }
    .hana-button-label .button-label.label-right {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 4px;
      border-top-left-radius: 0;
      border-top-right-radius: 4px;
      order: 2; }
  .hana-button-label .button-label-default {
    background-color: #ccd0d7;
    border: 1px solid #ccd0d7;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    color: #fff;
    padding: 5px 12px; }
    .hana-button-label .button-label-default > i {
      vertical-align: initial; }
  .hana-button-label > a {
    border: 1px solid #ccd0d7;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    color: #ccc;
    font-weight: bold;
    padding: 5px 20px;
    text-align: center;
    text-decoration: none; }
    .hana-button-label > a.label-right {
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 0;
      border-top-left-radius: 4px;
      border-top-right-radius: 0; }
    .hana-button-label > a:hover {
      color: #01b5fd; }
