.hana-menu {
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: #fff;
  color: #333; }

.hana-submenu {
  cursor: pointer; }
  .hana-submenu-title {
    display: block;
    padding: 10px;
    position: relative;
    transition: all 0.2s ease-in; }
    .hana-submenu-title:hover {
      color: #6cf; }
      .hana-submenu-title:hover::after {
        color: #6cf; }
      .hana-submenu-title:hover .hanaicon {
        color: #6cf; }
    .hana-submenu-title::after {
      content: "\E657";
      font-family: "hanaicon";
      font-size: 12px;
      color: #666;
      position: absolute;
      right: 10px;
      transition: all 0.3s linear; }
    .hana-submenu-title .hanaicon {
      margin-right: 5px;
      color: #666;
      vertical-align: top;
      transition: all 0.2s ease-in; }
  .hana-submenu-active > .hana-submenu-title::after {
    color: #6cf;
    transform: rotate(180deg); }
  .hana-submenu-disabled {
    color: #ccc;
    cursor: not-allowed; }
    .hana-submenu-disabled .hana-submenu-title {
      color: #ccc; }
      .hana-submenu-disabled .hana-submenu-title::after {
        color: #ccc; }
  .hana-submenu-elements {
    border-top: 1px solid #ccc;
    padding: 5px 0 5px 10px;
    transition: opacity .3s ease-in-out;
    transform-origin: left top;
    overflow: hidden; }
  .hana-submenu-transition-enter {
    opacity: 0; }
    .hana-submenu-transition-enter-active {
      opacity: 1; }
  .hana-submenu-transition-exit {
    opacity: 1; }
    .hana-submenu-transition-exit-active {
      opacity: 0; }

.hana-menuitem {
  font-size: 14px;
  padding: 10px 0 10px 10px;
  cursor: pointer;
  transition: all 0.3s ease-in-out; }
  .hana-menuitem .hanaicon {
    font-size: 14px;
    color: #666;
    margin-right: 5px;
    vertical-align: top;
    transition: all 0.2s ease-in; }
  .hana-menuitem:hover, .hana-menuitem-active {
    color: #6cf; }
    .hana-menuitem:hover .hanaicon, .hana-menuitem-active .hanaicon {
      color: #6cf; }
  .hana-menuitem-disabled {
    color: #ccc;
    cursor: not-allowed; }
    .hana-menuitem-disabled:hover {
      color: #ccc; }

.hana-menu-horizonal .hana-menuitem {
  display: inline-block;
  padding: 10px; }

.hana-menu-horizonal.hana-menu-default .hana-menuitem {
  border-right: 1px solid #ccc; }

.hana-menu-linear {
  border: none; }
  .hana-menu-linear.hana-menu-vertical .hana-menuitem {
    border-right: 2px solid transparent; }
  .hana-menu-linear.hana-menu-vertical .hana-menuitem-active {
    border-color: #6cf; }
  .hana-menu-linear.hana-menu-horizonal .hana-menuitem {
    border-bottom: 2px solid transparent; }
  .hana-menu-linear.hana-menu-horizonal .hana-menuitem-active {
    border-color: #6cf; }
