.hana-switch {
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  position: relative;
  cursor: pointer;
  margin-left: 5px; }
  .hana-switch::before {
    content: "";
    font-family: "hanaicon";
    display: inline-block;
    color: #b3b3b3;
    font-size: 24px;
    -webkit-font-smoothing: antialiased;
    position: absolute;
    left: -8px;
    top: -8px;
    transform: rotate(45deg);
    transition: all 0.4s ease-in;
    -webkit-transition: all 0.4s ease-in; }
  .hana-switch::after {
    content: "";
    display: block;
    margin-left: 10px;
    width: 20px;
    height: 8px;
    border-radius: 4px;
    background-color: #ccc;
    transition: all 0.4s ease-in;
    -webkit-transition: all 0.4s ease-in; }
  .hana-switch:hover::before {
    border-color: #6cf; }
  .hana-switch.hana-switch-active::before {
    border-color: #6cf;
    color: #33bbff;
    left: 23px;
    transform: rotate(135deg); }
  .hana-switch.hana-switch-active::after {
    background-color: #6cf; }
  .hana-switch.hana-switch-disabled {
    cursor: not-allowed; }
    .hana-switch.hana-switch-disabled::before {
      color: #ccc; }
    .hana-switch.hana-switch-disabled::after {
      background-color: #ccc; }
