.hana-checkbox-inner {
  box-sizing: content-box;
  display: inline-block;
  position: relative;
  margin-right: 10px;
  font-size: 16px;
  width: 15px;
  height: 15px;
  border: 1px solid #ccc;
  border-radius: 2px;
  vertical-align: middle; }
  .hana-checkbox-inner::after {
    content: "";
    position: absolute;
    box-sizing: border-box;
    left: 5px;
    top: 1px;
    width: 5px;
    height: 10px;
    border-color: #0088cc;
    transform: rotate(45deg);
    transition: all 0.3s ease-in; }

.hana-checkbox-custom {
  display: inline-block;
  margin-right: 10px;
  font-size: 16px;
  vertical-align: middle; }

.hana-checkbox {
  display: inline-block;
  cursor: pointer;
  position: relative;
  font-size: 14px; }
  .hana-checkbox span {
    vertical-align: middle;
    line-height: 30px; }
  .hana-checkbox:hover .hana-checkbox-inner {
    border-color: #6cf; }

.hana-checkbox-checked .hana-checkbox-inner {
  border: 1px solid #6cf; }

.hana-checkbox-checked .hana-checkbox-inner::after {
  border-color: #6cf;
  border-style: solid;
  border-width: 0 1px 1px 0;
  display: block; }

.hana-checkbox-disabled {
  cursor: not-allowed;
  color: #ccc; }
  .hana-checkbox-disabled:hover .hana-checkbox-inner {
    border-color: #ccc; }
  .hana-checkbox-disabled.hana-checkbox-checked .hana-checkbox-inner {
    border-color: #ccc; }
    .hana-checkbox-disabled.hana-checkbox-checked .hana-checkbox-inner::after {
      border-color: #ccc; }

.hana-checkbox-group .hana-checkbox {
  margin-right: 10px; }
