.hana-tag {
  display: inline-block;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
  height: 24px;
  line-height: 22px;
  padding: 0 5px;
  color: #666;
  background: #fff;
  margin-right: 10px;
  cursor: pointer; }
  .hana-tag:hover {
    border-color: #6cf; }
  .hana-tag .hanaicon {
    margin-left: 3px; }
    .hana-tag .hanaicon:hover {
      color: #6cf;
      cursor: pointer; }

.hana-tag-small {
  font-size: 12px;
  height: 16px;
  line-height: 14px; }

.hana-tag-large {
  font-size: 16px;
  height: 34px;
  line-height: 32px; }
