.hana-progress {
  height: 10px;
  background: #ccc;
  border-radius: 5px;
  margin-top: 24px; }
  .hana-progress-small {
    height: 6px; }
  .hana-progress-large {
    height: 16px; }
  .hana-progress-inner {
    border-radius: 5px;
    background: #6cf;
    height: inherit;
    transition: 0.2s width ease-in-out;
    position: relative; }
  .hana-progress-icon {
    position: absolute;
    right: -9px;
    top: -4px;
    color: #00aaff;
    transition: all 0.2s linear;
    text-align: center;
    font-size: 14px; }
    .hana-progress-icon .hana-progress-percent {
      position: absolute;
      line-height: 14px;
      height: 14px;
      top: -18px;
      right: 0; }
    .hana-progress-icon .hanaicon {
      font-size: 20px;
      height: 20px;
      display: inline-block; }
