.hana-pagination {
  list-style: none;
  text-align: center;
  box-sizing: border-box; }
  .hana-pagination-each {
    transition: all 0.3s linear;
    display: inline-block;
    cursor: pointer;
    color: #808080;
    border-color: #ccc;
    box-sizing: border-box; }
    .hana-pagination-each-large {
      font-size: 16px;
      min-width: 40px;
      height: 40px;
      line-height: 38px;
      margin: 0 8px; }
    .hana-pagination-each-middle {
      font-size: 14px;
      min-width: 30px;
      height: 30px;
      line-height: 28px;
      margin: 0 6px; }
    .hana-pagination-each-small {
      font-size: 12px;
      min-width: 22px;
      height: 22px;
      line-height: 20px;
      margin: 0 4px; }
    .hana-pagination-each-disabled {
      cursor: not-allowed;
      border-color: rgba(166, 166, 166, 0.8);
      color: #ccc; }
    .hana-pagination-each-hover {
      border-color: #1ab3ff;
      color: #6cf; }
    .hana-pagination-each-active {
      border-color: #1ab3ff;
      color: #6cf; }
    .hana-pagination-each-box {
      border-width: 1px;
      border-style: solid;
      border-radius: 4px; }
    .hana-pagination-each-circle {
      border-width: 1px;
      border-style: solid;
      border-radius: 50%; }
    .hana-pagination-each-box, .hana-pagination-each-circle {
      background-color: none;
      color: #808080; }
    .hana-pagination-each-box.hana-pagination-each-disabled, .hana-pagination-each-circle.hana-pagination-each-disabled {
      background-color: rgba(204, 204, 204, 0.8);
      color: #fff; }
    .hana-pagination-each-box.hana-pagination-each-hover, .hana-pagination-each-circle.hana-pagination-each-hover {
      background-color: #6cf;
      color: #fff; }
    .hana-pagination-each-box.hana-pagination-each-active, .hana-pagination-each-circle.hana-pagination-each-active {
      background-color: #6cf;
      color: #fff; }
    .hana-pagination-each-simple.hana-pagination-each-active {
      color: #6cf; }
  .hana-pagination-icon-default-pre {
    font-family: "hanaicon"; }
  .hana-pagination-icon-default-next {
    font-family: "hanaicon"; }
  .hana-pagination-jumper {
    display: inline-block; }
  .hana-pagination-jumper.hana-pagination-each-large {
    width: 84px; }
  .hana-pagination-jumper.hana-pagination-each-middle {
    width: 72px; }
  .hana-pagination-jumper.hana-pagination-each-small {
    width: 56px; }
