.hana-select {
  display: inline-block;
  vertical-align: top;
  position: relative;
  min-width: 100px;
  width: 100%;
  font-size: 14px; }
  .hana-select .hana-select-selection {
    background: #fff;
    box-sizing: border-box;
    border: 1px solid #ccc;
    height: 30px;
    min-height: 30px;
    line-height: 30px;
    border-radius: 2px;
    padding-left: 10px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: all 0.3s ease-in; }
  .hana-select-multiple.hana-select .hana-select-selection {
    height: auto;
    white-space: normal; }
  .hana-select-small {
    font-size: 12px; }
    .hana-select-small .hana-select-selection {
      height: 22px;
      min-height: 22px;
      line-height: 22px; }
  .hana-select-large {
    font-size: 16px; }
    .hana-select-large .hana-select-selection {
      height: 40px;
      min-height: 40px;
      line-height: 40px; }
  .hana-select-arrow {
    color: #666;
    position: absolute;
    right: 10px;
    top: -1px;
    transition: all 0.3s linear; }
    .hana-select-arrow .hanaicon {
      font-size: 12px; }
  .hana-select-down .hana-select-optwrap {
    top: 29px; }
  .hana-select-down.hana-select-small .hana-select-optwrap {
    top: 21px; }
  .hana-select-down.hana-select-large .hana-select-optwrap {
    top: 39px; }
  .hana-select-up .hana-select-optwrap {
    bottom: 29px; }
  .hana-select-up.hana-select-small .hana-select-optwrap {
    bottom: 21px; }
  .hana-select-up.hana-select-large .hana-select-optwrap {
    bottom: 39px; }

.hana-select-optwrap {
  z-index: 1024;
  box-sizing: border-box;
  border-radius: 2px;
  width: 100%;
  position: absolute;
  left: 0;
  border: 1px solid #ccc;
  background: #fff;
  transition: opacity 0.3s ease-in;
  overflow-y: auto; }
  .hana-select-optwrap.hana-select-transition-enter {
    opacity: 0; }
  .hana-select-optwrap.hana-select-transition-enter-active {
    opacity: 1; }
  .hana-select-optwrap.hana-select-transition-exit {
    opacity: 1; }
  .hana-select-optwrap.hana-select-transition-exit-active {
    opacity: 0; }
  .hana-select-optwrap .hana-select-option {
    line-height: 30px;
    padding-left: 10px;
    cursor: pointer;
    border-top: 1px solid #f4f4f4; }
    .hana-select-optwrap .hana-select-option:first-child {
      border-top: none; }
    .hana-select-optwrap .hana-select-option:hover {
      background-color: rgba(200, 200, 200, 0.2); }
    .hana-select-optwrap .hana-select-option:last-child {
      border-radius: 0 0 2px 2px; }
    .hana-select-optwrap .hana-select-option.hana-select-option-selected {
      background-color: rgba(200, 200, 200, 0.2); }
    .hana-select-optwrap .hana-select-option.hana-select-option-disabled {
      color: #ccc;
      cursor: not-allowed; }

.hana-select-small .hana-select-option {
  line-height: 22px; }

.hana-select-large .hana-select-option {
  line-height: 40px; }

.hana-select-active .hana-select-selection {
  border-color: #6cf;
  border-radius: 2px 2px 0 0;
  box-shadow: 2px 2px 3px 0 rgba(30, 30, 30, 0.15); }
  .hana-select-active .hana-select-selection .hana-select-arrow {
    color: #6cf;
    transform: rotate(-180deg); }

.hana-select-active .hana-select-optwrap {
  border-color: #6cf;
  border-radius: 0 0 2px 2px;
  box-shadow: 2px 2px 3px 0 rgba(30, 30, 30, 0.15); }

.hana-select-active.hana-select-up .hana-select-selection {
  border-top-color: transparent;
  border-bottom-color: #6cf; }

.hana-select-active.hana-select-up .hana-select-optwrap {
  border-radius: 2px 2px 0 0;
  border-top: 1px solid #6cf;
  box-shadow: 2px 0 3px 0 rgba(30, 30, 30, 0.15); }

.hana-select-disabled .hana-select-selection {
  background: #eee;
  color: #ccc;
  cursor: not-allowed; }
