.hana-tooltip {
  display: inline-block;
  position: relative; }
  .hana-tooltip-node {
    display: inline-block; }
  .hana-tooltip-content {
    z-index: 1024;
    position: fixed;
    line-height: 16px;
    font-size: 12px;
    padding: 5px;
    text-align: center;
    border-radius: 4px;
    word-break: break-all; }
    .hana-tooltip-content:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0; }
    .hana-tooltip-content-enter {
      opacity: 0; }
    .hana-tooltip-content-enter.hana-tooltip-content-enter-active {
      opacity: 1;
      transition: opacity 400ms ease-out; }
    .hana-tooltip-content-leave {
      opacity: 1; }
    .hana-tooltip-content-leave.hana-tooltip-content-leave-active {
      opacity: 0;
      transition: opacity 400ms ease-out; }
    .hana-tooltip-content-border {
      background-color: #fff;
      border: 1px solid #0099e6;
      color: #808080; }
    .hana-tooltip-content-fill {
      background-color: rgba(0, 153, 230, 0.9);
      border-color: rgba(0, 153, 230, 0.9);
      color: #fff; }
    .hana-tooltip-content-top {
      transform: translate(-50%, -100%);
      margin-top: -8px; }
      .hana-tooltip-content-top:after {
        top: 100%;
        left: 50%;
        margin-left: -4px;
        border-top: 4px solid;
        border-color: inherit;
        border-right: 4px solid transparent;
        border-left: 4px solid transparent; }
    .hana-tooltip-content-bottom {
      transform: translate(-50%, 4px);
      margin-top: 4px; }
      .hana-tooltip-content-bottom:after {
        top: -4px;
        left: 50%;
        margin-left: -4px;
        border-bottom: 4px solid;
        border-color: inherit;
        border-right: 4px solid transparent;
        border-left: 4px solid transparent; }
    .hana-tooltip-content-left {
      transform: translate(calc(-100%), -50%);
      margin-left: -8px; }
      .hana-tooltip-content-left:after {
        top: 50%;
        left: 100%;
        margin-top: -4px;
        border-left: 4px solid;
        border-color: inherit;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent; }
    .hana-tooltip-content-right {
      transform: translate(4px, -50%);
      margin-left: 4px; }
      .hana-tooltip-content-right:after {
        top: 50%;
        left: -4px;
        margin-top: -4px;
        border-right: 4px solid;
        border-color: inherit;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent; }
