.hana-sidebar {
  background-color: #fff;
  bottom: 0;
  overflow: auto;
  position: fixed;
  border: 1px solid #ddd;
  top: 0;
  width: 400px;
  z-index: 9; }
  .hana-sidebar::-webkit-scrollbar {
    width: 5px; }
  .hana-sidebar::-webkit-scrollbar-track {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.22); }
  .hana-sidebar::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.22); }
  .hana-sidebar.hana-sidebar-open {
    opacity: 1;
    transform: translateX(0);
    transition: 0.5s ease-in-out; }
  .hana-sidebar.hana-sidebar-right {
    right: 0; }
    .hana-sidebar.hana-sidebar-right.hana-sidebar-close {
      opacity: 0;
      transform: translateX(100%);
      transition: 0.5s ease-in-out; }
  .hana-sidebar.hana-sidebar-left {
    left: 0; }
    .hana-sidebar.hana-sidebar-left.hana-sidebar-close {
      opacity: 0;
      transform: translateX(-100%);
      transition: 0.5s ease-in-out; }
