.hana-notification .notification-content {
  word-break: break-all;
  word-wrap: break-word; }

.hana-notification {
  bottom: auto;
  height: auto;
  left: 50%;
  max-width: 320px;
  min-width: 300px;
  position: fixed;
  top: 20%;
  z-index: 2048; }
  .hana-notification .hana-notification-item {
    background-color: #fff;
    border-radius: 4px;
    margin-bottom: 10px;
    min-width: 350px;
    padding: 20px;
    transform: translate(-50%, -50%); }
  .hana-notification .notification-icon {
    cursor: pointer;
    position: absolute;
    right: 5px;
    top: 5px; }
  .hana-notification .notification-content {
    font-size: 18px;
    line-height: 20px; }

.hana-notification-enter {
  margin-top: 20px;
  opacity: 0; }
  .hana-notification-enter.hana-notification-enter-active {
    margin-top: 0;
    opacity: 1;
    transition: 0.25s ease-in; }

.hana-notification-leave {
  margin-top: 0;
  opacity: 1;
  transform: translate(-50%, -50%); }
  .hana-notification-leave.hana-notification-leave-active {
    margin-top: -20px;
    opacity: 0;
    transition: 0.25s ease-in; }

.hana-notification-info {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08); }

.hana-notification-success {
  border: 1px solid #4fc478;
  box-shadow: 0 2px 2px 0 rgba(79, 196, 120, 0.16), 0 0 0 1px rgba(79, 196, 120, 0.08);
  color: #4fc478; }

.hana-notification-error {
  border: 1px solid #d56767;
  box-shadow: 0 2px 2px 0 rgba(213, 103, 103, 0.16), 0 0 0 1px rgba(213, 103, 103, 0.08);
  color: #d56767; }

.hana-notification-warning {
  border: 1px solid #d56767;
  box-shadow: 0 2px 2px 0 rgba(213, 103, 103, 0.16), 0 0 0 1px rgba(213, 103, 103, 0.08);
  color: #e8964a; }
