.hana-link {
  text-decoration: none;
  transition: all .3s ease-in-out;
  color: #808080 !important; }
  .hana-link:hover {
    color: #6cf !important; }
  .hana-link-small {
    font-size: 12px; }
  .hana-link-middle {
    font-size: 14px; }
  .hana-link-large {
    font-size: 16px; }
  .hana-link .hanaicon {
    margin-right: 2px;
    color: inherit; }
