.hana-text-area {
  width: 100%;
  position: relative; }
  .hana-text-area-with-icon {
    margin-top: 8px; }

.hana-text-area-content {
  width: 100%;
  height: 100%; }

.hana-text-area-input {
  box-sizing: border-box;
  border: 1px solid;
  border-radius: 5px;
  outline: none;
  background: transparent;
  color: #808080;
  font-weight: 300;
  width: 100%;
  max-width: 100%;
  height: 100%;
  transition: color 0.5s linear, border-color 0.5s linear, height 0.3s ease-out; }
  .hana-text-area-input-large {
    font-size: 16px;
    line-height: 20px;
    padding: 20px 8px; }
  .hana-text-area-input-middle {
    font-size: 14px;
    line-height: 18px;
    padding: 18px 6px; }
  .hana-text-area-input-small {
    font-size: 12px;
    line-height: 16px;
    padding: 16px 4px; }
  .hana-text-area-input-normal {
    border-color: #cccccc; }
  .hana-text-area-input-focus {
    border-color: rgba(102, 204, 255, 0.9); }
  .hana-text-area-input-error {
    border-color: rgba(213, 103, 103, 0.9); }
  .hana-text-area-input-warning {
    border-color: rgba(232, 150, 74, 0.9); }
  .hana-text-area-input-success {
    border-color: rgba(79, 196, 120, 0.9); }
  .hana-text-area-input-disabled {
    border-color: rgba(204, 204, 204, 0.9);
    cursor: not-allowed; }

.hana-text-area-desc {
  display: -webkit-box;
  display: -o-box;
  display: -ms-box;
  display: -box;
  display: -pie;
  display: flex;
  transition: color 0.5s linear, border-color 0.5s linear, height 0.3s ease-out;
  position: absolute;
  padding-left: 2px;
  padding-right: 2px; }
  .hana-text-area-desc-large {
    top: -10px;
    left: 16px; }
  .hana-text-area-desc-middle {
    top: -8px;
    left: 14px; }
  .hana-text-area-desc-small {
    top: -6px;
    left: 12px; }
  .hana-text-area-desc-normal {
    color: rgba(204, 204, 204, 0.9); }
  .hana-text-area-desc-focus {
    color: rgba(102, 204, 255, 0.9); }
  .hana-text-area-desc-error {
    color: rgba(213, 103, 103, 0.9); }
  .hana-text-area-desc-warning {
    color: rgba(232, 150, 74, 0.9); }
  .hana-text-area-desc-success {
    color: rgba(79, 196, 120, 0.9); }
  .hana-text-area-desc-disabled {
    color: rgba(204, 204, 204, 0.9); }

.hana-text-area-icon {
  white-space: nowrap;
  box-sizing: border-box; }
  .hana-text-area-icon-default {
    font-family: "hanaicon";
    font-style: normal; }
  .hana-text-area-icon-large {
    font-size: 16px;
    line-height: 20px; }
  .hana-text-area-icon-middle {
    font-size: 14px;
    line-height: 18px; }
  .hana-text-area-icon-small {
    font-size: 12px;
    line-height: 16px; }

.hana-text-area-hint {
  display: block;
  line-height: 1;
  width: 100%;
  padding-top: 2px;
  padding-left: 4px;
  text-align: right;
  word-break: normal;
  overflow: hidden; }
  .hana-text-area-hint-large {
    font-size: 16px;
    height: 18px; }
  .hana-text-area-hint-middle {
    font-size: 14px;
    height: 16px; }
  .hana-text-area-hint-small {
    font-size: 12px;
    height: 14px; }
