.hana-modal-main .hana-modal-title-wrapper .icon-close, .hana-modal-main .hana-modal-content + .icon-close {
  cursor: pointer;
  position: absolute;
  right: 15px; }

.hana-modal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 512;
  -webkit-box-pack: center; }
  .hana-modal-overlay {
    background-color: rgba(0, 0, 0, 0.46);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 512; }
  .hana-modal-divider {
    margin: 0 auto; }
  .hana-modal-main {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 4px;
    min-height: 130px;
    min-width: 600px;
    justify-content: center;
    align-self: center;
    margin: 0 auto;
    z-index: 512; }
    .hana-modal-main .hana-modal-title-wrapper {
      font-size: 18px;
      min-height: 38px;
      position: relative; }
      .hana-modal-main .hana-modal-title-wrapper .hana-modal-title {
        padding: 10px 15px; }
      .hana-modal-main .hana-modal-title-wrapper .icon-close {
        top: 50%;
        transform: translateY(-50%); }
    .hana-modal-main .hana-modal-content {
      margin-bottom: 35px;
      max-height: 635px;
      overflow: auto;
      padding: 15px 20px;
      overflow-y: auto; }
    .hana-modal-main .hana-modal-button {
      bottom: 5px;
      position: absolute;
      right: 5px; }
      .hana-modal-main .hana-modal-button > button {
        margin-right: 5px; }

.hana-overlay-enter {
  opacity: 0;
  transition: all 0.15s ease-in; }
  .hana-overlay-enter.hana-overlay-enter-active {
    transition: all 0.15s ease-in;
    opacity: 1; }

.hana-overlay-exit {
  opacity: 0;
  transition: all 0.15s ease-in; }
  .hana-overlay-exit.hana-overlay-exit-active {
    transition: all 0.15s ease-in;
    opacity: 1; }

.hana-modal-enter {
  opacity: 0;
  transition: all 0.15s ease-in; }
  .hana-modal-enter.hana-modal-enter-active {
    transition: all 0.15s ease-in;
    opacity: 1; }

.hana-modal-exit {
  opacity: 1;
  transition: all 0.15s ease-in; }
  .hana-modal-exit.hana-modal-exit-active {
    transition: all 0.15s ease-in;
    opacity: 0; }
