.hana-title {
  border-radius: 4px;
  cursor: default;
  font-size: 18px;
  font-weight: bold;
  padding: 0 10px;
  position: relative; }
  .hana-title-content {
    display: inline-block;
    line-height: 1.2;
    vertical-align: middle; }
  .hana-title i {
    display: inline-block;
    font-family: hanaicon;
    font-size: 30px;
    font-style: inherit;
    margin-right: 8px; }
  .hana-title div[class^='icon-'] {
    cursor: pointer;
    display: inline-block;
    vertical-align: bottom; }
  .hana-title .icon-right {
    position: absolute;
    right: 10px; }
  .hana-title-sub {
    font-size: 14px;
    font-weight: 100;
    line-height: 1.6; }
