@keyframes loading-rotate {
  from {
    transform: rotateZ(0deg); }
  to {
    transform: rotateZ(-360deg); } }

@keyframes loading-image {
  from {
    clip-path: polygon(0 100%, 0 100%, 100% 100%, 100% 100%); }
  to {
    clip-path: polygon(0 0, 0 100%, 100% 100%, 100% 0); } }

.hana-loading-wrap {
  position: fixed;
  background: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2048; }

.hana-loading {
  position: fixed;
  padding: 10px;
  min-height: 80px;
  min-width: 80px;
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid #6cf;
  border-radius: 5px;
  color: #6cf;
  text-align: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); }
  .hana-loading .hanaicon {
    display: inline-block;
    font-size: 20px;
    padding: 10px; }
  .hana-loading-rotate .hanaicon {
    animation: loading-rotate 2s linear infinite; }
  .hana-loading-icons .hanaicon {
    opacity: 0; }
    .hana-loading-icons .hanaicon:nth-child(1) {
      animation: loading-queue-item-1 2.5s ease-in-out infinite; }

@keyframes loading-queue-item-1 {
  from {
    opacity: 0; }
  15% {
    opacity: 0; }
  30% {
    opacity: 1; }
  80% {
    opacity: 1; }
  to {
    opacity: 0; } }
    .hana-loading-icons .hanaicon:nth-child(2) {
      animation: loading-queue-item-2 2.5s ease-in-out infinite; }

@keyframes loading-queue-item-2 {
  from {
    opacity: 0; }
  30% {
    opacity: 0; }
  45% {
    opacity: 1; }
  80% {
    opacity: 1; }
  to {
    opacity: 0; } }
    .hana-loading-icons .hanaicon:nth-child(3) {
      animation: loading-queue-item-3 2.5s ease-in-out infinite; }

@keyframes loading-queue-item-3 {
  from {
    opacity: 0; }
  45% {
    opacity: 0; }
  60% {
    opacity: 1; }
  80% {
    opacity: 1; }
  to {
    opacity: 0; } }
    .hana-loading-icons .hanaicon:nth-child(4) {
      animation: loading-queue-item-4 2.5s ease-in-out infinite; }

@keyframes loading-queue-item-4 {
  from {
    opacity: 0; }
  60% {
    opacity: 0; }
  75% {
    opacity: 1; }
  80% {
    opacity: 1; }
  to {
    opacity: 0; } }
  .hana-loading-image > img {
    display: block;
    position: relative;
    width: 100%;
    height: auto;
    clip-path: polygon(0 0, 0 0, 0 0, 0 0);
    animation: loading-image 2.5s ease-in-out infinite; }

.hana-loading-content {
  margin-top: 15px;
  font-size: 14px; }
