.hana-popover {
  max-width: none;
  padding: 0; }
  .hana-popover-top {
    padding: 8px; }
  .hana-popover-title {
    display: block;
    text-align: left;
    width: calc(100% - 20px);
    font-size: 16px; }
  .hana-popover-sub-title {
    font-size: 12px; }
  .hana-popover-close {
    position: absolute;
    top: 8px;
    right: 4px; }
    .hana-popover-close-icon {
      font-family: "hanaicon";
      font-size: 16px;
      color: #4dc4ff; }
  .hana-popover-content {
    padding: 8px; }
  .hana-popover-border .hana-popover-hr {
    background-color: #6cf;
    height: 1px; }

.hana-popover.hana-tooltip-content {
  z-index: 1024; }
  .hana-popover.hana-tooltip-content-top:after {
    border: none; }
  .hana-popover.hana-tooltip-content-bottom:after {
    border: none; }
  .hana-popover.hana-tooltip-content-left:after {
    border: none; }
  .hana-popover.hana-tooltip-content-right:after {
    border: none; }

.hana-popover.hana-tooltip-content-border {
  border: 1px solid #6cf; }
