.hana-time-picker-select-pre:before {
  content: ""; }

.hana-time-picker-select-next:before {
  content: ""; }

.hana-time-picker-select-icon {
  font-family: 'hanaicon';
  font-size: 14px;
  font-style: normal;
  vertical-align: middle; }

