.hana-time-picker-warp {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }

.hana-time-picker-container-container {
  position: fixed;
  z-index: 1024;
  margin-top: 4px;
  width: 400px;
  padding: 24px 6px 6px 6px;
  border: 1px solid #6cf;
  border-radius: 4px;
  background: #fff;
  overflow: hidden; }

.hana-time-picker-container .hana-time-picker-container-enter {
  opacity: 0; }

.hana-time-picker-container .hana-time-picker-container-enter-active {
  opacity: 1;
  transition: all 400ms ease-out; }

.hana-time-picker-container .hana-time-picker-container-exit {
  opacity: 1; }

.hana-time-picker-container .hana-time-picker-container-exit-active {
  opacity: 0;
  transition: all 400ms ease-out; }

.hana-time-picker-content {
  display: flex;
  width: 100%;
  height: 96px;
  margin: auto;
  color: #000;
  font-size: 28px;
  font-weight: 200; }

.hana-time-picker-hour, .hana-time-picker-minute, .hana-time-picker-second {
  position: relative;
  width: 33.3%;
  height: 100%; }

.hana-time-picker-v-line {
  display: none;
  font-size: 24px;
  font-weight: 300;
  line-height: 42px; }

.hana-time-picker-select {
  display: flex;
  align-items: center;
  height: 100%; }
  .hana-time-picker-select-container {
    height: 28px; }
  .hana-time-picker-select-icon {
    display: block;
    position: absolute;
    width: 25%;
    height: 28px;
    line-height: 28px;
    color: #888;
    font-size: 13px;
    cursor: pointer; }
  .hana-time-picker-select-pre {
    text-align: right;
    font-family: "hanaicon"; }
  .hana-time-picker-select-content {
    position: absolute;
    width: 50%;
    margin-left: 25%;
    text-align: center;
    cursor: pointer;
    transition: all 0.4s linear; }
  .hana-time-picker-select-next {
    text-align: left;
    margin-left: 75%;
    font-family: "hanaicon"; }
  .hana-time-picker-select .hana-time-picker-select-close-pre-enter {
    opacity: 0;
    margin-top: -12px; }
  .hana-time-picker-select .hana-time-picker-select-close-pre-enter-active {
    opacity: 1;
    margin-top: 0;
    transition: all 400ms ease-out; }
  .hana-time-picker-select .hana-time-picker-select-close-pre-exit {
    opacity: 1;
    margin-top: 0; }
  .hana-time-picker-select .hana-time-picker-select-close-pre-exit-active {
    opacity: 0;
    margin-top: 12px;
    transition: all 200ms ease-out; }
  .hana-time-picker-select .hana-time-picker-select-close-next-enter {
    opacity: 0;
    margin-top: 12px; }
  .hana-time-picker-select .hana-time-picker-select-close-next-enter-active {
    opacity: 1;
    margin-top: 0;
    transition: all 400ms ease-out; }
  .hana-time-picker-select .hana-time-picker-select-close-next-exit {
    opacity: 1;
    margin-top: 0; }
  .hana-time-picker-select .hana-time-picker-select-close-next-exit-active {
    opacity: 0;
    margin-top: -12px;
    transition: all 200ms ease-out; }
  .hana-time-picker-select-open {
    overflow: auto;
    height: 100%;
    top: 0;
    background-color: #fff; }
    .hana-time-picker-select-open::-webkit-scrollbar {
      width: 8px;
      background: none; }
    .hana-time-picker-select-open::-webkit-scrollbar-thumb {
      background-color: #888; }
  .hana-time-picker-select .hana-time-picker-select-open-enter {
    opacity: 0; }
  .hana-time-picker-select .hana-time-picker-select-open-enter-active {
    opacity: 1;
    transition: all 400ms ease-out; }
  .hana-time-picker-select .hana-time-picker-select-open-exit {
    opacity: 1; }
  .hana-time-picker-select .hana-time-picker-select-open-exit-active {
    opacity: 0;
    transition: all 200ms ease-out; }
  .hana-time-picker-select-open .hana-time-picker-select-option {
    font-size: 14px;
    padding: 4px 0; }
    .hana-time-picker-select-open .hana-time-picker-select-option:hover {
      background-color: rgba(102, 204, 255, 0.6); }
    .hana-time-picker-select-open .hana-time-picker-select-option-current {
      background-color: rgba(102, 204, 255, 0.6); }

.hana-time-picker-action-buttons {
  padding-top: 18px;
  width: 95%;
  text-align: right; }

.hana-time-picker-confirm-button, .hana-time-picker-cancel-button, .hana-time-picker-clear-button {
  border: none;
  background-color: transparent;
  color: #888;
  text-align: center;
  font-size: 14px;
  padding: 6px 12px;
  height: 28px; }
  .hana-time-picker-confirm-button:hover, .hana-time-picker-cancel-button:hover, .hana-time-picker-clear-button:hover {
    color: #444;
    background-color: rgba(221, 221, 221, 0.4); }
