.hana-postcard {
  width: 100%;
  border: 1px solid #eee;
  box-shadow: 2px 3px 10px 0 rgba(30, 30, 30, 0.2);
  position: relative;
  z-index: 1;
  display: inline-block;
  background: #fff;
  transition: 0.3s all ease-in-out; }
  .hana-postcard:hover {
    z-index: 99;
    border-color: #6cf; }
  .hana-postcard-title {
    padding: 20px 10px 10px;
    font-size: 18px; }
  .hana-postcard-subtitle {
    font-size: 14px;
    color: #999;
    margin-left: 5px; }
  .hana-postcard-content {
    padding: 10px; }
  .hana-postcard-group .hana-postcard {
    margin: 0 5px; }

.hana-postcard-group-clothesline .hana-postcard {
  transform: perspective(600px) rotateY(30deg); }
  .hana-postcard-group-clothesline .hana-postcard:hover {
    transform: none; }
