.hana-image-tiny {
  width: 50px; }
  .hana-image-tiny.hana-image-circular {
    height: 50px; }

.hana-image-small {
  width: 100px; }
  .hana-image-small.hana-image-circular {
    height: 100px; }

.hana-image-middle {
  width: 150px; }
  .hana-image-middle.hana-image-circular {
    height: 150px; }

.hana-image-large {
  width: 250px; }
  .hana-image-large.hana-image-circular {
    height: 250px; }

.hana-image-huge {
  width: image-huge-size; }
  .hana-image-huge.hana-image-circular {
    height: image-huge-size; }

.hana-image {
  display: inline-block;
  vertical-align: middle;
  height: auto; }

.hana-image-circular {
  border-radius: 50%; }

.hana-imagegroup {
  padding: 5px;
  display: inline-block; }
  .hana-imagegroup .hana-image {
    margin: 0 5px; }
