.hana-card {
  margin-bottom: 40px;
  padding-top: 10px;
  position: relative;
  background: #fff; }
  .hana-card .hana-card-container {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 15px 15px 10px;
    transition: all 0.3s ease-in-out; }
    .hana-card .hana-card-container:hover {
      border-color: #6cf;
      box-shadow: 2px 2px 3px 0 rgba(30, 30, 30, 0.15); }
  .hana-card .hana-card-title {
    color: #01b5fd;
    position: absolute;
    font-size: 16px;
    top: 0;
    left: 15px;
    background: #fff;
    padding: 0 10px; }
    .hana-card .hana-card-title .hanaicon {
      margin-right: 10px;
      vertical-align: top; }
  .hana-card-subtitle {
    color: #999;
    font-size: 14px;
    margin-left: 5px; }
  .hana-card-expand {
    padding-top: 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out; }
    .hana-card-expand:hover {
      border-color: #6cf;
      box-shadow: 2px 2px 3px 0 rgba(30, 30, 30, 0.15); }
  .hana-card-expand .hana-card-title {
    color: #333;
    padding-left: 10px;
    height: 40px;
    line-height: 40px;
    position: relative; }
    .hana-card-expand .hana-card-title > .hanaicon {
      margin-right: 10px;
      vertical-align: top; }
  .hana-card-expand .hana-card-container {
    overflow: hidden;
    border-top: 1px solid transparent;
    max-height: 0;
    opacity: 0;
    transition: 0.5s opacity ease-in-out, 0.5s border-color ease-in-out; }
  .hana-card-expand .hana-card-container-wrap {
    padding: 10px; }
  .hana-card-expand .hana-card-expand-icon-wrap {
    position: absolute;
    right: 10px;
    top: 0; }
  .hana-card-expand .hana-card-expand-icon {
    display: block;
    color: #999;
    cursor: pointer;
    transition: 0.3s transform linear; }
    .hana-card-expand .hana-card-expand-icon:hover {
      color: #6cf; }
  .hana-card-expand-open .hana-card-expand-icon {
    color: #6cf;
    transform: rotate(180deg); }
  .hana-card-expand-open .hana-card-container {
    border-color: #ccc;
    opacity: 1;
    max-height: none; }
