.hana-carousel {
  overflow: hidden;
  position: relative; }
  .hana-carousel-dot {
    display: inline-block;
    background: rgba(102, 204, 255, 0.6);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer; }
    .hana-carousel-dot-active {
      background: #6cf; }
  .hana-carousel-option-control {
    box-sizing: border-box;
    font-style: normal;
    color: #6cf;
    position: absolute;
    background: rgba(255, 255, 255, 0.8);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 14px;
    border: 1px solid rgba(102, 204, 255, 0.5);
    cursor: pointer; }
    .hana-carousel-option-control:hover {
      background: #6cf; }
      .hana-carousel-option-control:hover:after {
        border-color: #fff; }
    .hana-carousel-option-control:after {
      content: '';
      display: block;
      width: 8px;
      height: 8px;
      border: 2px solid #6cf;
      border-width: 2px 0 0 2px; }
  .hana-carousel-horizontal .hana-carousel-dot-group {
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    text-align: center; }
  .hana-carousel-horizontal .hana-carousel-option-control {
    top: 50%;
    margin-top: -20px;
    line-height: 42px; }
    .hana-carousel-horizontal .hana-carousel-option-control:hover {
      color: #00aaff; }
  .hana-carousel-horizontal .hana-carousel-option-prev {
    left: 0;
    margin-left: -20px;
    padding-left: 20px; }
    .hana-carousel-horizontal .hana-carousel-option-prev:after {
      margin: 14px 0 0 5px;
      transform: rotate(-45deg); }
  .hana-carousel-horizontal .hana-carousel-option-next {
    right: 0;
    margin-right: -20px;
    padding-left: 5px; }
    .hana-carousel-horizontal .hana-carousel-option-next:after {
      margin: 14px 0 0 0;
      transform: rotate(135deg); }
  .hana-carousel-slide .hana-carousel-item {
    float: left; }
  .hana-carousel-slide.hana-carousel-vertical .hana-carousel-item {
    display: block; }
  .hana-carousel-fade .hana-carousel-item {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    transition: .6s opacity ease-in-out; }
  .hana-carousel-fade .hana-carousel-item-active {
    opacity: 1; }
  .hana-carousel-vertical .hana-carousel-dot-group {
    position: absolute;
    width: 20px;
    top: 50%;
    transform: translateY(-50%);
    right: 0; }
  .hana-carousel-vertical .hana-carousel-option-control {
    text-align: center;
    left: 50%;
    margin-left: -20px; }
  .hana-carousel-vertical .hana-carousel-option-prev {
    top: 0;
    margin-top: -20px; }
    .hana-carousel-vertical .hana-carousel-option-prev:after {
      margin: 24px 0 0 14px;
      transform: rotate(45deg); }
  .hana-carousel-vertical .hana-carousel-option-next {
    bottom: 0;
    margin-bottom: -20px; }
    .hana-carousel-vertical .hana-carousel-option-next:after {
      margin: 4px 0 0 14px;
      transform: rotate(-135deg); }
