.hana-slider {
  background: #ccc;
  height: 10px;
  border-radius: 5px;
  position: relative;
  font-size: 12px;
  margin-top: 20px; }
  .hana-slider-small {
    height: 6px; }
  .hana-slider-large {
    height: 16px; }
  .hana-slider-inner {
    background: #6cf;
    height: inherit;
    border-radius: inherit;
    position: relative; }
  .hana-slider-button {
    position: absolute;
    right: -11px;
    top: -5px;
    background-color: #00aaff;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    cursor: pointer; }
    .hana-slider-button:hover {
      background-color: #0088cc; }
  .hana-slider-value {
    user-select: none;
    position: absolute;
    right: -11px;
    top: -20px;
    color: #6cf; }
  .hana-slider-icon-wrap {
    position: absolute;
    right: 0;
    top: 5px;
    transform: translate(50%, -50%);
    cursor: pointer;
    color: #00aaff; }
    .hana-slider-icon-wrap .hanaicon {
      color: inherit;
      font-size: 24px; }
  .hana-slider-small .hana-slider-icon-wrap {
    top: 3px; }
    .hana-slider-small .hana-slider-icon-wrap .hanaicon {
      font-size: 20px; }
  .hana-slider-large .hana-slider-icon-wrap {
    top: 8px; }
    .hana-slider-large .hana-slider-icon-wrap .hanaicon {
      font-size: 30px; }
