.hana-audio {
  box-sizing: border-box;
  background: #fff;
  width: 600px;
  height: 300px;
  border-radius: 10px;
  border: 1px solid #6cf;
  position: relative;
  padding: 25px 25px 25px 325px; }
  .hana-audio-poster {
    display: block;
    position: absolute;
    width: 275px;
    height: 250px;
    left: 25px;
    top: 25px;
    z-index: 1; }
  .hana-audio-title {
    text-align: center;
    font-size: 16px;
    color: #808080;
    height: 50px; }
  .hana-audio-options {
    padding-left: 55px;
    height: 50px; }
    .hana-audio-options .hanaicon {
      cursor: pointer;
      font-size: 14px;
      box-sizing: border-box;
      display: inline-block;
      border-radius: 50%;
      color: #fff;
      width: 30px;
      height: 30px;
      line-height: 32px;
      padding-left: 7px;
      background-color: #99ddff;
      transition: background-color .3s ease-in-out; }
      .hana-audio-options .hanaicon:hover {
        background-color: #6cf; }
    .hana-audio-options .hana-audio-play {
      font-size: 20px;
      width: 40px;
      height: 40px;
      line-height: 40px;
      background: #6cf;
      padding-left: 11px;
      margin: 0 20px; }
      .hana-audio-options .hana-audio-play:hover {
        background: #33bbff; }
      .hana-audio-options .hana-audio-play.icon-pause {
        padding-left: 10px; }
  .hana-audio-time {
    height: 50px; }
    .hana-audio-time-current, .hana-audio-time-duration {
      display: inline-block;
      padding: 3px 0;
      font-size: 14px; }
    .hana-audio-time-duration {
      float: right; }
  .hana-audio-volume {
    height: 50px; }
    .hana-audio-volume .hanaicon {
      color: #6cf;
      font-size: 16px; }
      .hana-audio-volume .hanaicon:nth-child(2) {
        float: right; }
    .hana-audio-volume .hana-audio-progress {
      margin-top: 5px; }
  .hana-audio-progress {
    font-size: 12px;
    width: 100%;
    height: 5px;
    background: #ccc;
    border-radius: 3px;
    cursor: pointer; }
    .hana-audio-progress:hover .hana-audio-progress-cube {
      opacity: 1; }
    .hana-audio-progress-active {
      border-radius: 3px;
      background: #6cf;
      position: relative;
      transition: width .1s ease-in-out; }
    .hana-audio-progress-cube {
      opacity: 0;
      position: absolute;
      top: -2px;
      right: -4px;
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background: #6cf;
      transition: .3s opacity ease-in-out; }
  .hana-audio-list {
    overflow: hidden;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    position: absolute;
    left: 25px;
    top: 25px;
    background: #fff;
    width: 275px;
    height: 250px; }
    .hana-audio-list-open {
      opacity: 1;
      z-index: 2; }
    .hana-audio-list-item {
      height: 36px;
      line-height: 36px;
      cursor: pointer;
      transition: opacity .3s ease-in-out; }
      .hana-audio-list-item:hover, .hana-audio-list-item-active {
        color: #6cf; }
  .hana-audio-mode {
    color: #808080;
    height: 45px;
    align-items: flex-end;
    display: flex; }
    .hana-audio-mode-active {
      color: #6cf; }
    .hana-audio-mode .hanaicon {
      font-size: 20px;
      text-align: center;
      flex: 1; }
      .hana-audio-mode .hanaicon:hover {
        color: #6cf;
        cursor: pointer; }
  .hana-audio-time .hanaicon, .hana-audio-volume .hanaicon, .hana-audio-mode .hanaicon {
    transition: color .3s ease-in-out; }
