.hana-radio-inner {
  box-sizing: content-box;
  display: inline-block;
  position: relative;
  margin-right: 10px;
  font-size: 16px;
  width: 15px;
  height: 15px;
  border: 1px solid #ccc;
  border-radius: 15px;
  vertical-align: middle;
  transition: all 0.2s ease-in; }
  .hana-radio-inner::after {
    position: absolute;
    content: "";
    left: 3px;
    top: 3px;
    width: 9px;
    height: 9px;
    border-radius: 9px;
    transition: all 0.2s ease-in; }

.hana-radio-custom {
  display: inline-block;
  margin-right: 10px;
  font-size: 16px;
  vertical-align: middle; }

.hana-radio {
  display: inline-block;
  cursor: pointer;
  position: relative;
  font-size: 14px; }
  .hana-radio span {
    vertical-align: middle;
    line-height: 30px; }
  .hana-radio:hover .hana-radio-inner {
    border-color: #6cf; }

.hana-radio-checked .hana-radio-inner {
  border: 1px solid #6cf; }

.hana-radio-checked .hana-radio-inner::after {
  display: block;
  background: #6cf; }

.hana-radio-disabled {
  cursor: not-allowed;
  color: #ccc; }
  .hana-radio-disabled:hover .hana-radio-inner {
    border-color: #ccc; }
  .hana-radio-disabled.hana-radio-checked .hana-radio-inner {
    border-color: #ccc; }
    .hana-radio-disabled.hana-radio-checked .hana-radio-inner::after {
      background: #ccc; }

.hana-radio-group .hana-radio {
  margin-right: 10px; }
